.Header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  position: fixed;
  width: 100%;
  height: 90px;
  z-index: 10;
}

.Header-bg {
  background-color: rgba(0, 0, 0, 0.5);
  transition: 0.5s;
}

.navbar {
  display: flex;
}
.navbar li {
  padding: 0 1rem;
}
.navbar li a {
  font-size: 1.3rem;
  font-weight: 700;
}
.button {
  display: none;
}
@media screen and (max-width: 1040px) {
  .navbar {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.99);
    position: absolute;
    top: 0;
    left: -100%;
    z-index: -3;
    transition: 0.3s;
  }
  .navbar.active {
    left: 0;
  }
  .navbar li {
    padding: 1rem 0;
  }
  .navbar li a {
    font-size: 2rem;
  }
  .button {
    display: initial;
  }
}
@media screen and (max-width: 768px) {
  .Header h1 {
    font-size: 1.4em;
  }
  .navbar li a {
    font-size: 1.4em;
  }
}
