.about-container {
  width: 100%;
  height: 100%;
  margin: 3rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
.about-container .about-left {
  text-align: center;
  margin: auto;
  padding: 1rem;
  max-width: 350px;
}
.about-container .about-left .foto {
  width: 50%;
}
.about-container .about-left p {
  margin: 1.2rem 0;
  font-size: 1rem;
}
.about-container .about-right {
  max-width: 1024px;
}
.about-right .img-container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  position: relative;
  align-items: center;
  text-align: center;
}
.about-right .img {
  max-width: 90%;
  border: 1px solid #333;
}
.about-right .img-top {
  grid-column: 1 / span 8;
  grid-row: 1;
  padding-top: 20%;
  z-index: 6;
}

.about-right .img-bot {
  grid-column: 4 / -1;
  grid-row: 1;
  z-index: 3;
}
