.layer {
  width: 100%;
  height: 100vh;
  position: relative;
}
.hero-images {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  object-fit: cover;
}

.layer::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #000;
  opacity: 0.8;
}
.hero-sec {
  height: 100%;
  width: 100%;
}
.hero-sec .intro {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  text-align: center;
}
.hero-sec .intro h1 {
  font-size: 3.5rem;
  padding: 0.6rem 0 1.5rem;
}
.hero-sec .intro p {
  font-size: 1.5rem;
  font-weight: 200;
}
.intro .btn {
  margin: 1rem 0.5rem;
}

@media screen and (max-width: 640px) {
  .hero-sec .intro h1 {
    font-size: 2.3rem;
  }
  .hero-sec .intro p {
    font-size: 1.2rem;
  }
}
