@media screen and (max-width: 1024px) {
  .project-container {
    max-width: 80%;
    margin: auto;
    grid-template-columns: 1fr;
  }

  .project-card {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    border-radius: 20px;

    color: #ffffff;
    text-align: center;
  }
  .popup {
    width: 90%;
    min-height: 90%;
    display: flex;
    flex-direction: column;
    padding: 20px;
    overflow: hidden;
  }
  .modal-container > h1 {
    font-size: 1em;
  }
}
