.hero-bg {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  height: 60vh;
  position: relative;
}
.hero-bg::before {
  content: "";
  background-image: url(../assets/wallpaper2.png);
  background-size: cover;
  background-position: center;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.heading {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.hero-bg h1 {
  font-size: 2.4rem;
}
.hero-bg p {
  font-size: 1.4rem;
  text-align: center;
}

@media screen and (max-width: 768px) {
  .hero-bg h1 {
    font-size: 1.5em;
  }
  .hero-bg p {
    font-size: 1em;
  }
  .hero-bg {
    height: 30vh;
  }
}
