.footer {
  width: 100%;
  padding: 6rem 0;
  background-color: rgba(19, 19, 19, 0.8);
}
.footer-container {
  max-width: 1140px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 40px;
}
.left-sec {
  height: 100%;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
}
.location {
  display: flex;
  margin-bottom: 0.8rem;
}
.location p {
  line-height: 30px;
}
.email {
  display: flex;
  margin-bottom: 0.8rem;
}
.phone {
  display: flex;
  margin-bottom: 0.8rem;
}
.linkedin {
  display: flex;
  margin-bottom: 0.8rem;
}

.right-sec {
  height: 100%;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
}
.right-sec p {
  text-align: justify;
}
.socials {
  margin-top: 1rem;
}
h4 {
  font-size: 1.3rem;
  padding-bottom: 0.7rem;
}
p {
  font-size: 1.1rem;
}

@media screen and (max-width: 768px) {
  .footer-container {
    grid-template-columns: 1fr;
  }

  .left-sec h4 {
    font-size: 1.2em;
  }
  .socials {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
  }
}
